import React, {Component} from 'react';
import {Container, Row, Col, Card} from 'react-bootstrap';
import Header from '../Header';
import logo_bootstrap from '../assets/bootstrap-logo.png';
import logo_react from '../assets/react-logo.png';
import github from '../assets/github.png';
import adobe_xd from '../assets/adobe-xd.png';
import whimsical from '../assets/whimsical.png';
import invision from '../assets/invision.png';
import github_grey from '../assets/github-grey.png';
import youtube_grey from '../assets/youtube-grey.png';
import dribble_grey from '../assets/dribble-grey.png';
import instagram_grey from '../assets/instagram-grey.png';
import bali from '../assets/bali-card.jpg';
import undraw_1 from '../assets/undraw_1.png';
import undraw_2 from '../assets/undraw_2.png';
import undraw_3 from '../assets/undraw_3.png';
import $ from 'jquery';

class Home extends Component {
  componentDidMount() {
    $("a[href^='#']").click(function(e) {
	     e.preventDefault();
	     var position = $($(this).attr("href")).offset().top - 50;

	      $("body, html").animate({
		        scrollTop: position
	         }, 500);
	        });
          
  }
  render(){
  document.title="Anshor's portfolio"
  return (
      <Container fluid className="px-0">
        <Header />
        <div id="front-image">
          <br />
          <br />
          <h3 id="front-text">"Design is not just what it looks like and feels like. Design is how it works." </h3>
        </div>
      <Row className="mx-0 mb-3" id="about">
        <Col md={6} sm={12} id="about-me">
          <h4 className="about-text">
            Hi!
          </h4>
          <p className="about-text">I'M ANSHOR</p>
          <p className="about-text-2">A FRON'T END DEVELOPER</p>
        </Col>
        <Col md={6} sm={12} id="skills">
          <p className="skills">SKILLS</p>
          <button id="html-b">
            HTML
          </button>
          <button id="css-b">
            CSS
          </button>
          <button id="javascript-b">
            JavaScript
          </button>
          <br />
          <br />
          <Row>
          <Col xs={12} md={3}>
            <p className="grey">Framework using</p>
            <img
              className="logo-2"
              src={logo_bootstrap}
              alt="logo bootstrap"
              />

              <img
                className="logo-3"
                src={logo_react}
                alt="logo react"
                />
            </Col>
            <Col xs={12} md={9}>
              <p className="grey">Tools</p>
              <img
                className="logo-4"
                src={adobe_xd}
                alt="adobe xd"
                />

                <img
                  className="logo-3"
                  src={invision}
                  alt="invision-app"
                  />

                  <img
                    className="logo-3"
                    src={whimsical}
                    alt="whimsical"
                    />

                  <img
                    className="logo-3"
                    src={github}
                    alt="github"
                    />
            </Col>
            </Row>
        </Col>
      </Row>
      <Row className="mx-0" id="projects">
        <Col className="col-12">
          <h4 className="projects">PROJECTS</h4>
          <p className="projects">Here's some projects that i've build</p>
        </Col>
        <Col md={3} xs={12} sm={6}>
          <Card>
            <Card.Img variant="top" src={bali}/>
            <Card.Body>
              <Card.Title>Beautiful Bali</Card.Title>
              <Card.Text>
                A landing page about Bali. Made with HTML, CSS, and Javascript. Using Bootsrap, Font-awesome and Jquery to make page more beautiful and interactive
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12} sm={6}>
          <Card>
            <Card.Img variant="top" src={undraw_1} />
            <Card.Body>
              <Card.Title>Coming Soon</Card.Title>
              <Card.Text>
                ...
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12} sm={6}>
          <Card>
            <Card.Img variant="top" src={undraw_2}/>
            <Card.Body>
              <Card.Title>Coming Soon</Card.Title>
              <Card.Text>
                ...
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12} sm={6}>
          <Card>
            <Card.Img variant="top" src={undraw_3}/>
            <Card.Body>
              <Card.Title>Coming Soon</Card.Title>
              <Card.Text>
                ...
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="mx-0" id="contact">
        <Col>
          <center>
            <h4 className="contact-text">Get in touch</h4>
            <p className="contact-text">Let me know if you need something</p>
            <a href="mailto:anshorje@gmail.com">anshorje@gmail.com</a>
            <br />
            <img
              src={youtube_grey}
              alt="youtube logo"
              className="grey_logo"
              />
            <img
                src={instagram_grey}
                alt="instagram logo"
                className="grey_logo"
                />
            <img
                src={github_grey}
                alt="github logo"
                className="grey_logo"
                />
            <img
                src={dribble_grey}
                alt="dribble logo"
                className="grey_logo"
                />
          </center>
        </Col>
      </Row>
    </Container>
  );
}
}

export default Home;
