import React, {Component} from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import logo from './assets/anshor.space.png';

class Header extends Component{
  render(){
    return(
      <Navbar fixed="top" className="bg-bluegradient" expand="lg" variant="dark">
        <Navbar.Brand >
          <img
            src={logo}
            alt="anshor.space logo"
            className="align-top logo"
            />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse >
          <Nav className="mr-auto">
          </Nav>
          <Nav >
            <a className="white-nav" href="#about">ABOUT</a>
            <a className="white-nav" href="#skills">SKILL</a>
            <a className="white-nav" href="#projects">PROJECT</a>
            <a className="white-nav" href="#contact">CONTACT</a>
          </Nav>

        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default Header;
